@import url('https://fonts.googleapis.com/css?family=Josefin+Sans&display=swap');

body{
   background-color: #f3f3f3;
}

.wrapper {
  display: flex;
  flex-direction: row;
  height: 100%;
  width: 100%;
}

.wrapper .left{
  width: 35%;
  background: #01dbdf;
  padding: 30px 25px;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  text-align: center;
  color: #fff;
}

.wrapper .left img{
  border-radius: 5px;
  margin-bottom: 10px;
}

.wrapper .left h4{
  margin-bottom: 10px;
}

.wrapper .left p{
  font-size: 12px;
}

.wrapper .right{
  width: 100%;
  background: #fff;
  padding: 30px 25px;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}

.wrapper .right .info,
.wrapper .right .projects{
  margin-bottom: 25px;
}

.wrapper .right .info h3,
.wrapper .right .projects h3{
    margin-bottom: 15px;
    padding-bottom: 5px;
    border-bottom: 1px solid #e0e0e0;
    color: #353c4e;
  text-transform: uppercase;
  letter-spacing: 5px;
}

.wrapper .right .info_data,
.wrapper .right .projects_data{
  display: flex;
  justify-content: space-between;
}

.wrapper .right .info_data .data,
.wrapper .right .projects_data .data{
  width: 45%;
}

.wrapper .right .info_data .data h4,
.wrapper .right .projects_data .data h4{
    color: #353c4e;
    margin-bottom: 5px;
}

.wrapper .right .info_data .data p,
.wrapper .right .projects_data .data p{
  font-size: 13px;
  margin-bottom: 10px;
  color: #919aa3;
}

.wrapper .social_media ul{
  display: flex;
}

.wrapper .social_media ul li{
  width: 45px;
  height: 45px;
  background: linear-gradient(to right,#01a9ac,#01dbdf);
  margin-right: 10px;
  border-radius: 5px;
  text-align: center;
  line-height: 45px;
}

.wrapper .social_media ul li a{
  color :#fff;
  display: block;
  font-size: 18px;
}

/* Modal overlay styles */
.userprofileCard-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent background */
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Modal content styles */
.userprofileCard-modal-content {
  background-color: #fff;
  padding: 20px;
  border-radius: 5px;
  width: 300px; /* Adjust the width as needed */
  text-align: center;
}

/* Close button styles */
.userprofileCard-close {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
}
.userprofileCard-custom-modal-dialog {
  max-width: 800px; /* Set the desired maximum width for the modal */
  width: 100%;
  margin: 1.75rem auto;
}
.userprofileCard-custom-modal-content {
  width: 100%;
}